.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 20px;
  background-color: #4a90e2;
  color: white;
  position: relative;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  z-index: 1000;
}

.header-left {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
}

.logo {
  height: 50px;
  cursor: pointer; /* Aggiunto il puntatore a mano sul logo */
}

.header-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin-top: 10px;
}

.search-form {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin: 10px 0;
}

.search-form input[type="text"] {
  flex: 1;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px 0 0 4px; /* Rimosso margin-right per unire input e pulsante */
  box-sizing: border-box;
}

.search-form button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-sizing: border-box;
}

.search-form button:hover {
  background-color: #0056b3; /* Hover per il pulsante di ricerca */
}

.suggestions {
  position: absolute;
  top: 100%;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #ccc;
  max-height: 200px;
  overflow-y: auto;
  width: 100%;
  max-width: 600px;
  list-style: none;
  padding: 0;
  margin: 0;
  z-index: 1001;
  box-sizing: border-box;
}

.suggestions li {
  padding: 10px;
  cursor: pointer;
}

.suggestions li:hover {
  background-color: #f0f0f0;
}

.header-right {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
}

.login-icon {
  background-color: #007bff;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border-radius: 4px;
  transition: background-color 0.3s ease-in-out;
  box-sizing: border-box;
}

.login-icon:hover {
  background-color: #0056b3;
}

.login-dropdown {
  position: absolute;
  top: calc(100% + 10px); 
  right: 0;
  background-color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  z-index: 1000;
  padding: 20px;
  width: 250px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.login-form input[type="text"],
.login-form input[type="password"] {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.login-form button {
  padding: 10px;
  background-color: #007bff;
  border: none;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  box-sizing: border-box;
}

.login-form button:hover {
  background-color: #0056b3;
}

.header-navigation {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;
}

.header-navigation a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  padding: 5px 10px;
  transition: background-color 0.3s;
  box-sizing: border-box;
}

.header-navigation a:hover {
  background-color: #357ab7;
  border-radius: 4px;
}
