footer {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 20px;
  background-color: #4a90e2;
  color: white;
  flex-wrap: wrap;
}

.footer-section {
  flex: 1;
  text-align: center;
  margin: 10px;
  min-width: 200px;
}

.contacts h3, .links h3, .social-media h3, .credits p {
  margin: 0 0 10px 0;
}

.contacts p, .links ul, .credits p {
  margin: 5px 0;
}

.contacts p {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contacts svg, .social-media svg {
  margin-right: 10px;
}

.contacts a, .credits a {
  color: white;
  text-decoration: none;
}

.links ul {
  list-style-type: none;
  padding: 0;
}

.links ul li {
  margin-bottom: 5px;
}

.links ul li a {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

.links ul li a:hover {
  color: #3498db;
}

.social-media .social-icons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.social-media .social-icons a {
  color: white;
  font-size: 1.5em;
  transition: color 0.3s ease;
}

.social-media .social-icons a:hover {
  color: #3498db;
}

.credits {
  font-size: 0.9em;
}
