.weather-today {
  margin: 20px;
  padding: 20px;
  background: url('https://images.unsplash.com/photo-1561470508-fd4df1ed90b2?q=80&w=2076&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D') no-repeat center center;
  background-size: cover;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  color: white;
  position: relative;
  z-index: 1;
}

.weather-today::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6); /* Darker overlay for better readability */
  border-radius: 8px;
  z-index: -1;
}

.weather-today h2 {
  margin-bottom: 20px;
  color: #fff;
  font-size: 24px;
}

.weather-today-city {
  margin-bottom: 10px;
  font-size: 22px;
  color: #ffd700;
}

.weather-today-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.weather-today-main {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.weather-today-icon {
  width: 100px;
  height: 100px;
  margin-right: 20px;
}

.weather-today-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.weather-today-temperature {
  font-size: 36px;
  font-weight: bold;
  color: #ffd700;
}

.weather-today-description {
  font-size: 18px;
  color: #ccc;
}

.weather-today-details {
  display: flex;
  justify-content: space-around;
  width: 100%;
  font-size: 16px;
  color: #ccc;
}

.weather-today-details p {
  margin: 0 10px;
}
