.news {
  margin: 20px;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 60px;
}

.news h2 {
  margin-bottom: 20px;
  color: #007bff;
}

.news-cards {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.news-card {
  background-color: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  display: flex;
  flex-direction: column;
}

.news-card img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.news-card h3 {
  margin: 10px;
  font-size: 24px;
}

.news-card p {
  margin: 10px;
  font-size: 16px;
  color: #555;
  flex: 1;
}

@media screen and (max-width: 1080px) {
  .news {
    margin: 10px;
    padding: 10px;
  }
}

@media screen and (max-width: 720px) {
  .news-card img {
    height: 200px; /* Riduci l'altezza dell'immagine per schermi più piccoli */
  }

  .news-card h3 {
    font-size: 20px; /* Riduci la dimensione del titolo per schermi più piccoli */
  }

  .news-card p {
    font-size: 14px; /* Riduci la dimensione del testo per schermi più piccoli */
  }
}
